<template>
  <div class="information full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div>
          <div class="information-title">{{ result.title }}</div>
          <div class="information-bar">
            <span>{{ result.time }}</span>
            <span>来源于{{ result.source }}</span>
          </div>
          <div class="information-content" v-html="result.content"></div>
          <div class="information-link" @click="toProjectDetail">{{btnContent}}</div>
          <br>
          <br>
          <br>
          <br>
        </div>
      </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { MiniSiteApi } from 'api'
import { setTitle, device } from 'common/js/util'

export default {
  data () {
    return {
      result: {},
      btnContent: '',
      projectDate: {}
    }
  },
  components: {
    PemScrollView
  },
  activated () {
    this._getInformation(this.$route.query.inforId)
  },
  created () {
    this._setTitle()
  },
  methods: {
    _getInformation (inforId) {
      MiniSiteApi.getInformation(inforId).then(data => {
        var obj = data.data.noticeEntity
        if (Number(data.resultcode) === 1) {
          this.result = {
            title: obj.title || '',
            time: obj.time || '',
            source: obj.source || '',
            link: obj.sourceurl || '',
            content: obj.detailscontent || '',
            type: obj.projectlocation,
            projectzj: obj.projectzj
          }
          this.projectDate = data.data.projectData
          if (obj.projectzj) {
            this.btnContent = '查看项目详情'
          } else if (this.result.link) {
            this.btnContent = '查看原文'
          }
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        }
      }).catch((error) => {
        console.error(error)
        // this.$router.push('/invalid')
      })
    },
    _setTitle () {
      setTitle('融资资讯')
    },
    toProjectDetail () {
      if (this.result.projectzj) {
        var pro = {type: this.result.type, data: this.projectDate}
        if (device.isIOS()) {
          window.webkit && window.webkit.messageHandlers && window.webkit.messageHandlers.getData.postMessage(pro)
        } else if (device.isAndroid()) {
          window.approot && window.approot.intenttoapp(JSON.stringify(pro))
        }
      } else if (this.result.link) {
        window.location.href = this.result.link
      }
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .information
    font-size: 0.24rem
    .page-panel
      background-color: $color-panel
      
    .information-title
      color: $color-text-title
      font-size: 0.44rem
      letter-spacing: -0.0082rem
      line-height: 0.6rem
      padding: 0.2rem 0.4rem
    .information-bar
      font-size: 0
      padding: 0 0.4rem
      span
        font-size: 0.28rem
        color: $color-text-caption
        letter-spacing: -0.0074rem
        margin-right: 0.28rem
        line-height: 0.4rem
    .information-content
      padding: 0.4rem 0.4rem 0.2rem 0.4rem
      font-size: 0.32rem
      color: $color-text-body
      letter-spacing: 0.0004rem
      line-height: 0.52rem
      img
        max-width: 6.7rem
    .information-link
      padding: 0 0.4rem 0.6rem 0.4rem
      font-size: 0.28rem
      color: $color-text-link
</style>



